<template>
  <div class="board_wrapper">
    <div class="board_content">
      <BoardItemGame
        :game-status="gameStatus"
        v-for="field in fields"
        :field="field"
        :key="'item-' + field.id"
        @selectField="selectField($event)"
      />
    </div>
    <p class="difficult">
      Складність: <strong>{{ difficult }}</strong>
    </p>
    <p class="win" v-if="isWin">Вітаємо! Продовжуємо гру далі!</p>
    <p class="fail" v-if="isFail">Ви програли. Спробуйте ще раз!</p>
    <button class="board_btn" @click="start" :disabled="!canStartGame">
      Старт
    </button>
  </div>
</template>

<script>
import BoardItemGame from "./BoardItemGame.vue";
import useGameInit from "./composables/useGameInit";
import useGameStart from "./composables/useGameStart";
import useGameProcess from "./composables/useGameProcess";
import { GAME_STATUS } from "@/constants";
import { ref } from "vue";
export default {
  name: "BoardGame",
  props: {},
  components: {
    BoardItemGame,
  },
  setup() {
    const number = 25;
    const gameStatus = ref(GAME_STATUS.NONE);
    const { difficult, fields, init } = useGameInit(number);
    const { start, canStartGame } = useGameStart(
      init,
      fields,
      difficult,
      number,
      gameStatus
    );

    const { selectField, isWin, isFail } = useGameProcess(
      fields,
      gameStatus,
      difficult,
      start
    );
    return {
      number,
      difficult,
      fields,
      init,
      start,
      gameStatus,
      canStartGame,
      selectField,
      isWin,
      isFail,
    };
  },
};
</script>

